import { required } from 'vuelidate/lib/validators';
export default {
  data() {
    return {
      itemData: {
        model: '',
        type: '',
        color: '',
      },
      showTop: false,
    };
  },
  validations: {
    itemData: {
      model: { required },
      type: { required },
      color: { required },
    },
  },
  methods: {
    submitForm() {
      this.$v.$touch();
    },
    showErrorAlert() {
      this.$store.commit('show_error_alert', 'Test message');
    },
  },
};
